<template>
	<section class="wrap has_header_wrap">
		<section class="wrap_in">
			<!-- 头部标题 -->
			<com-header titles="慢症药馆">
				<template  slot="style-def"></template>
			</com-header>
			<!-- 头部标题 end -->
			
			<!-- banner -->
			<!-- <div class="banner">
				<img :src="banners[0].advertisementPhoto" alt="">
			</div> -->
			<div class="banner">
				<mt-swipe :auto="4000">
				  <mt-swipe-item v-for="(it,index) in banners" :key="index">
				  	<a :href="it.advertisementUrl">
				  		<img :src="it.advertisementPhoto" alt="">
				  	</a>
				  </mt-swipe-item>
				</mt-swipe>
			</div>
			<!-- banner end -->

			<!-- 爆款特惠 -->
			<div class="bkth" v-show="hlTl.length">
				<h2><span>肿瘤科爆款特惠</span></h2>
				<p>为癌症患者助力</p>
			</div>
			
			<div class="bkth_list" v-if="hlTl.length>0">
				<div class="top d_flex">
					<div class="tl" @click="jumpDetail(hlTl[0])">
						<img v-lazy="hlTl[0].productPhotos[0].photoURL" :alt="hlTl[0].productName">
						<img v-if="hlTl[0].productPhotos.length<=0" src="../../../static/images/ak.png" :alt="hlTl[0].productName">
						<h3>{{ hlTl[0].sellingPoint?hlTl[0].sellingPoint:'' }}</h3>
						<span>{{ hlTl[0].productName }}</span>
					</div>
					<div class="tr" v-if="hlTr.length>0">
						<div 
							class="d_flex" 
							v-for="(htr,index) in hlTr"
							@click="jumpDetail(htr)">
							<div>
								<h3>{{ htr.sellingPoint?htr.sellingPoint:'' }}</h3>
								<span>{{ htr.productName }}</span>
								<h2><span class="r_color">{{ htr.ourPrice | priceFilter }}</span></h2>
							</div>
							<div>
								<img v-if="htr.productPhotos.length>0" v-lazy="htr.productPhotos[0].photoURL" :alt="htr.productName">
								<img v-if="htr.productPhotos.length<=0" src="../../../static/images/ak.png" :alt="htr.productName">
							</div>
						</div>
					</div>
				</div>

				<div class="bottom">
					<ul class="d_flex">
						<li 
							class="d_flex" 
							v-for="(hlb,index) in hlBottom"
							@click="jumpDetail(hlb)">
							<div>
								<h3>{{ hlb.sellingPoint?hlb.sellingPoint:'' }}</h3>
								<span>{{ hlb.productName }}</span>
								<h2><span class="r_color">{{ hlb.ourPrice | priceFilter }}</span></h2>
							</div>
							<div>
								<img v-if="hlb.productPhotos.length>0" v-lazy="hlb.productPhotos[0].photoURL" :alt="hlb.productName">
								<img v-if="hlb.productPhotos.length<=0" src="../../../static/images/ak.png" :alt="hlb.productName">
							</div>
						</li>
						<!-- <li class="d_flex"  @click="jumpDetail(hotList[4])">
							<div>
								<h3>{{ hotList[4].sellingPoint?hotList[0].sellingPoint:'' }}</h3>
								<span>{{ hotList[4].productName }}</span>
								<h2><span class="r_color">{{ hotList[4].ourPrice | priceFilter }}</span></h2>
							</div>
							<div>
								<img v-if="hotList[4].productPhotos.length>0" v-lazy="hotList[4].productPhotos[0].photoURL" :alt="hotList[4].productName">
								<img v-if="hotList[4].productPhotos.length<=0" src="../../../static/images/ak.png" :alt="hotList[4].productName">
							</div>
						</li> -->
					</ul>
				</div>
			</div>
			<!-- 爆款特惠 end -->

			<!-- 产品列表 -->
			<div class="pros"  v-if="recomList.length">
				<ul class="d_flex">
					<li v-for="(rl,index) in recomList"  @click="jumpDetail(rl)">
						<img v-if="rl.productPhotos && rl.productPhotos.length>0" v-lazy="rl.productPhotos[0].photoURL" :alt="rl.productName">
						<img v-if="rl.productPhotos && rl.productPhotos.length<=0" src="../../../static/images/ak.png" :alt="rl.productName">
						<h3>{{ rl.productName }}</h3>
						<span>{{ rl.spec }}</span>
						<h2><span class="r_color">{{ rl.ourPrice | priceFilter }}</span></h2>
						<!-- 套餐暂不需要 -->
						<div v-if="false" class="activitys">
							<span class="inlie_block">套餐</span> 
							<span class="inlie_block">秒杀</span> 
							<span class="inlie_block">满减</span>
						</div>
					</li>
				</ul>
			</div>
			<!-- 产品列表 end -->
		</section>

		<!-- 子页面 -->
	    <transition name="fade">
	    	<router-view v-on:changeClass="changeClassOne"></router-view>
	    </transition>
	    <!-- 子页面 end -->
	</section>
</template>
<script>
import { Header, Button, Badge, Swipe, SwipeItem } from 'mint-ui';
import comHeaderTitle from '@/components/Common/ComHeaderTitle'

export default {
	name: 'myinfo',
	data() {
		return {
	      isUnscRollable: false, //是否页面不可滚动
	      backParentPage: false, //是否回到父页面
	      banners: [],//轮播图
	      hotList: [],//爆款特惠
	      // hotList2: {
	      // 	'tl': [],//左上位
	      // 	'tr': [],//右上位
	      // 	'bottom': [],//下推荐
	      // },//爆款特惠
	      BaoKuan: true,//否是为爆款类型推荐楼层
	      hlTl: [],//左上位
	      hlTr: [],//右上位
	      hlBottom: [],//下推荐
	      recomList: [],//商品推荐
	      t1: [],
	      t2: [],
	      t3: []
		}
	},
	props: [
		// 'pclass',
	],
	filters: {
		//价格过滤
		priceFilter ( data ){
			if( data || data === 0 ){
				return '￥' + Number(data).toFixed(2);
			}else{
				return '';
			}
		},
		//产品图片
		// showImg ( data ){
		// 	// <img v-if="rl.productPhotos.length>0" v-lazy="rl.productPhotos[0].photoURL" :alt="rl.productName"
		// 	if( data.productPhotos ){
		// 		if( data.productPhotos.length>0 ){
		// 			return data.productPhotos[0].photoURL;
		// 		}else{
		// 			return '../../../static/images/ak.png';
		// 		}
		// 	}else{
		// 		return '../../../static/images/ak.png';
		// 	}
		// }
	},
	computed: {
		//产品图片
		showImg (){
			if(this.$data.recomList){
		    	if( this.$data.recomList.productPhotos.length >0 ){
		    		return 1
		    	}else{
		    		return 0;
		    	}
			}
		},
	},
	components: {
		'mt-header': Header,
		'mt-button': Button,
		'mt-badge':  Badge,
		comHeaderTitle, // 公共头部标题
		'mt-swipe': Swipe,
		'mt-swipe-item': SwipeItem,
	},
	created : function(){
		const _self = this;
	    var myDate = new Date();//获取系统当前时间
	    var y = myDate.getFullYear();
	    var m = myDate.getMonth() + 1;
	    if( m < 10 ){
	      m = '0'+m;
	    }
	    var d = myDate.getDate();
	    if( d < 10 ){
	      d = '0'+d;
	    }
	    var datas = y + '-' + m + '-' + d + ' ' + '23:59:59';

	    //轮播图模块
	    this.$ajax({
	      url: _self.webRoot + '/seachAd',
	      // url: '/seachAd',
	      data: {
	        endDate: datas,
	        pageNo: 'channelMzBanner',
	        isValid: 1
	      }
	    })
	    .then(function(res){
	      // console.log('channelMzBanner: ',res)
	      if( res.data.success ){
	        _self.banners = res.data.data;
	      }else{
	        _self.banners = '';
	        console.log('banner模块不存在')
	      }
	    })
	    .catch(function(err){
	      console.log(err)
	    });

	    //爆款推荐
	    this.$options.methods.getProRecomBk( _self, datas, 'channelMzBk', datas, _self.BaoKuan, '爆款推荐模块无数据' );

	    //产品推荐
	    this.$options.methods.getProRecom( _self, datas, 'channelMzTj', datas, _self.recomList, '商品推荐模块无数据' );
	},
	mounted : function() {
		const _self = this;
		this.$emit('changeClass',false);
	},
	updated (){
	    this.$nextTick(function () {
	      if( !this.backParentPage ){
	        // this.isUnscRollable = true;
	      }
	    }) 
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
  	methods: {

  		//跳转到详情页
  		jumpDetail ( item ){
  			this.$router.push({path: '/product_detail', query: {proid: item.productID}});
  		},
	    // 获取模块数据 - 商品推荐类型楼层
	    getProRecom ( _self, endDate1, pageNo, endDate2, moduleData, errMsg ){
	      _self.$ajax({
	        url: _self.webRoot + '/seachSt',
	        // url: '/seachSt',
	        data: {
	          endDate: endDate1,
	          pageNo: pageNo,
	          isValid: 1
	        }
	      })
	      .then(function(res){
	      	if(!res.data.data[0]){return}
	      	// console.log(111,res.data.data[0])
	        var showId = res.data.data[0].showID;

	        if( res.data.success ){
	          _self.$ajax({
	            url: _self.webRoot + '/seachStDetail',
	            // url: '/seachStDetail',
	            data: {
	              endDate: endDate2,
	              showID: showId,
	              isValid: 1
	            }
	          })
	          .then(function(res){
	            // console.log(moduleData)
	            if( res.data.success ){
	              moduleData.splice(0); //先移除数据
	              var productIdList = [];

	              for( let x in res.data.data ){
	                productIdList.push( res.data.data[x].productID );
	              }

	              _self.$ajax({
	                url: _self.webRoot + '/product/getProductByProductNumber',
	                data: JSON.stringify({
	                  memberId: _self.$store.state.memberId,
	                  productNumbers: productIdList,
	                }),
	                headers: {'Content-Type' : 'application/json;charset=utf-8'},
	                transformRequest: [function (data) {
	                    return data
	                }]
	              })
	              .then(function(res){
	                // console.log('productNumbers: ', res)
	                if( res.data.success && res.data.data.length>0 ){
	                  for( let x in res.data.data ){
	                    moduleData.push(res.data.data[x])
	                  }
	                }
	                if( !res.data.success ){
	                  moduleData = [];
	                  console.log(errMsg)
	                }
	                // console.log('akRecommend', moduleData)
	              })
	              .catch(function(err){
	                console.log(err)
	              });
	            }else{
	              moduleData = [];
	              console.log(errMsg)
	            }
	          })
	          .catch(function(err){
	            console.log(err)
	          });
	        }else{
	          moduleData = [];
	          console.log(errMsg)
	        }
	      })
	      .catch(function(err){
	        console.log(err)
	      });
	    },
	    // 获取模块数据 - 爆款类型楼层
	    getProRecomBk ( _self, endDate1, pageNo, endDate2, moduleData, errMsg ){
	      _self.$ajax({
	        url: _self.webRoot + '/seachSt',
	        // url: '/seachSt',
	        data: {
	          endDate: endDate1,
	          pageNo: pageNo,
	          isValid: 1
	        }
	      })
	      .then(function(res){
	      	if(!res.data.data[0]){return}
	      	// console.log(111,res.data.data[0])
	        var showId = res.data.data[0].showID;

	        if( res.data.success ){
	          _self.$ajax({
	            url: _self.webRoot + '/seachStDetail',
	            // url: '/seachStDetail',
	            data: {
	              endDate: endDate2,
	              showID: showId,
	              isValid: 1
	            }
	          })
	          .then(function(res){
	            // console.log('type: ', res.data.data[0].type)

              	//商品推荐
	            if( res.data.success && res.data.data[0].type === 2 ){
	              _self.tl = _self.tr = _self.bottom = [];//先移除数据
	              	
	      //         	var allData = res.data.data;
	      //         	allData.forEach(function(value,index,array){
	      //         		_self.$ajax({
							// url: _self.webRoot + '/product/getProductByProductNumber',
							// data: {
							// productNumber: value.productID,
							// 	memberId: _self.$store.state.memberId,
							// }
	      //         		})
	      //         		.then(function(res){
							// if( index === 0 ) {
							// 	_self.hlTl.push(res.data.data)
							// }else if ( index >=1 && index < 3 ){
							// 	_self.hlTr.push(res.data.data)
							// }else {
							// 	_self.hlBottom.push(res.data.data)
							// }
	      //         		})
	      //         		.catch(function(err){
	      //         			console.log(err)
	      //         		});
	      //         	});
	      		var productIdList = [];
				for( let x in res.data.data ){
					productIdList.push( res.data.data[x].productID );
				}

				_self.$ajax({
					url: _self.webRoot + '/product/getProductByProductNumber',
					data: JSON.stringify({
					  memberId: _self.$store.state.memberId,
					  productNumbers: productIdList,
					}),
					headers: {'Content-Type' : 'application/json;charset=utf-8'},
					transformRequest: [function (data) {
					    return data
					}]
				})
				.then(function(res){
					console.log('productNumbersbbbbb: ', res)
					if( res.data.success && res.data.data.length>0 ){
					  for( let index in res.data.data ){
					    if( index == 0 ) {
					    	console.log('daaaa: ', res.data.data)
							_self.hlTl.push(res.data.data[index])
						}else if ( index >=1 && index < 3 ){
							_self.hlTr.push(res.data.data[index])
						}else {
							_self.hlBottom.push(res.data.data[index])
						}
					  }
					}
					if( !res.data.success ){
					  moduleData = [];
					  console.log(errMsg)
					}
				})
				.catch(function(err){
					console.log(err)
				});
	            }else{
        		  if( moduleData === true ) { _self.tl = _self.tr = _self.bottom = []; return; }
	              moduleData = [];
	              console.log(res.data.msg)
	            }
	          })
	          .catch(function(err){
	            console.log(err)
	          });
	        }else{
	          moduleData = [];
	          console.log(errMsg)
	        }
	      })
	      .catch(function(err){
	        console.log(err)
	      });
	    },
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
  	}
}
</script>

<style scoped>
	@import './Css/channel.css';
</style>


